.alg-dtwp-loading-tab{
	&:after{
		opacity:0 !important;
	}
	&.alg-dtwp-loaded:after{
		opacity:1 !important;
	}
	&.alg-dtwp-loaded a:after{
		opacity:0 !important;
		right:-1em;
	}
	a:after{
		content:" "!important;
		position:absolute;
		top:50%;
		right:1em;
		display:block;
		border: 3px solid #ddd;
		border-radius: 50%;
		border-top: 3px solid #000;
		width: 15px;
		height: 15px;
		margin-top:-11px;
		-webkit-animation: alg-dtwp-spin 1.3s linear infinite; /* Safari */
		animation: alg-dtwp-spin 1.3s linear infinite;
		transition: opacity ease .3s, right ease .3s;
		opacity:1;
	}
}

@keyframes alg-dtwp-spin {
	0% { transform: rotate(0deg); }
	100% { transform: rotate(360deg); }
}